const MediaMsg = ({ el , menu}) => {
    const theme = useTheme();
    return (
        <Stack direction={"row"} justifyContent={el.incoming ? "start" : "end"}
        >
            <Box p={1.5} sx={{
                backgroundColor: el.incoming
                    ? theme.palette.background.default
                    : theme.palette.primary.main,
                borderRadius: 1.5, // 1.5*8 => 12px
                width: "max-content",
            }}
            >
                <Stack spacing={1}>
                    <img src={el.img}
                        alt={el.message}
                        style={{
                            maxWidth: "100%", // Ensure the image doesn't exceed the box width
                            maxHeight: "210px", // Set maximum height
                            borderRadius: "10px",

                        }}
                    />
                    <Typography variant="body2" color={el.incoming ? theme.palette.text : "#fff"}>
                        {el.message}
                    </Typography>
                </Stack>
            </Box>
            {menu &&  <MessageOptions/>}
        </Stack>
    )
}

const TextMsg = ({ el, menu }) => {
    const theme = useTheme();
    return (
        <Stack direction={"row"} justifyContent={el.incoming ? "start" : "end"}>
            <Box p={1.5} sx={{
                backgroundColor: el.incoming
                    ? theme.palette.background.default
                    : theme.palette.primary.main,
                borderRadius: 1.5, // 1.5*8 => 12px
                width: "max-content",
            }}>
                <Typography variant="body2" color={el.incoming ? theme.palette.text : "#fff"}>
                    {el.message}
                </Typography>
            </Box>
            {/*  */}
            {menu &&  <MessageOptions/>}
        </Stack>
    );
};

const Timeline = ({ el }) => {
    const theme = useTheme();

    return (
        <Stack direction={"row"} alignItems="center" justifyContent="space-between">
            <Divider width="46%" />
            <Typography variant="caption" sx={{ color: theme.palette.text }} >{el.text}</Typography>
            <Divider width="46%" />
        </Stack>
    );
};

const MessageOptions = () => {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    return (
        <>
            <DotsThreeVertical 
             id="basic-button"
             aria-controls={open ? 'basic-menu' : undefined}
             aria-haspopup="true"
             aria-expanded={open ? 'true' : undefined}
             onClick={handleClick}
             size={20}/>
            <Menu
                id="basic-menu"
                anchorEl={anchorEl} 
                open={open}
                onClose={handleClose}
                MenuListProps={{
                    'aria-labelledby': 'basic-button',
                }}
            >
                <Stack spacing={1} px={1}>
                    {Message_options.map((el) => (
                        <MenuItem onClick={() => { /* Perform some action here */ }}>{el.title}</MenuItem>
                    ))}
                </Stack>
            </Menu>
        </>
    );
}

export { Timeline, TextMsg, MediaMsg, ReplyMsg, LinkMsg, DocMsg };


